@import './assets/constants.scss';

@font-face {
  font-family: 'Neutral Face';
  src:
    url('./assets/fonts/neutralface-bold-webfont.woff') format('woff'),
    url('./assets/fonts/neutralface-bold-webfont.woff2') format('woff2'),
    url('./assets/fonts/NeutralFace-Bold.otf') format('opentype');
  font-weight: bold;
}

html {
    font-size: 20px;
}

body {
    width: 100%;
    height: 100%;
    font-family: $general, sans-serif;
    margin: 0;
}

html,
#root {
    // background-color: $gray;
    width: 100%;
    height: 100%;
}

#root {
    margin: auto;
    max-width: 410px;
    height: 100%;
}

.launch-screen {
    height: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    .logo-center {
        height: 51px;
        position: absolute;
        z-index: 4;
        animation: gradientLoad 2s ease-in-out 1; // with delay = 4s
        // animation-delay: 3s;
    }
    .logo-left, .logo-right {
        width: 70px;
        margin: auto;
        position: absolute;
        z-index: 2;
    }
    .logo-left {
        top: -58px;
        bottom: 0;
        left: -67.2px;
        &:not(:first-of-type) {
            top: 0;
        }
    }
    .logo-right {
        top: 0;
        right: -65px;
        bottom: -55px;
        &:not(:last-of-type) {
            bottom: 0;
        }
    }
}

@keyframes gradientLoad {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 10;
    padding: 0 .25em;
    background-color: $white; //$gray;
    h1 {
        display: flex;
        flex-direction: column;
        margin-right: 10px;
        text-transform: uppercase;
        font-weight: normal;
        font-family: 'Neutral Face','Inter',sans-serif;
        font-size: 1.1rem;
        span {
            line-height: 22px;
        }
        img {
            margin: 0 4px;
            height: 17px;
        }
    }
    .favs {
        height: 25px;
        display: none;
    }
}

a {
    text-decoration: none;
    color: black;
}


.discount-item {
    display: grid;
}

.flat-divs {
    display: grid;
    gap: 4px;
    grid-template-columns: repeat(4, 1fr);
}

.flat-div {
    width: auto;
    display: flex;
    flex-direction: column;
    button {
        height: 20px;
        margin: 10px 0.25em;
    }
}

.flat-title {
    font-size: .45rem;
    font-family: 'Neutral Face', 'Inter', sans-serif;
    font-weight: bold;
    text-transform: uppercase;
}

*{
    outline: none;
}

.line {
    width: 30px;
    margin: 0 10px;
    position: relative;
    top: 3px;
    display: inline !important;
}

.launch-screen {
    display: flex;
    justify-content: center;
    h1 {
        display: flex;
        flex-direction: column;
        text-transform: uppercase;
        font-weight: normal;
        font-family: 'Neutral Face','Inter',sans-serif;
        font-size: 1.1rem;
        margin-right: 10px;
        span {
            line-height: 22px;
        }
        img {
            margin: 0 4px;
            height: 17px;
        }
    }
}

.signup-slider-page_content-wrapper {
    height: 82% !important;
}

.signup-pages-slider {
    height: 100%;
    .slick-list {
        height: 100%;
        .slick-track {
            height: 100%;
            div {
                height: 100%; //auto; //
                .signup-slider-page {
                    height: 100%; //calc(100% - 1.5em*2);
                    position: relative;
                    padding: 0; //1.5em 0;
                    width: 100%;
                    display: flex !important;
                    flex-direction: column;
                    justify-content: space-evenly;

                    &--cafe_restaurants {
                        background-color: #d3081c;
                    }
                    &--supermarkets_hypermarkets {
                        background-color: #0cb248;
                    }
                    &--coffee_house_bakery {
                        background-color: #f9c008;
                    }
                    &--craft_products_stores {
                        background-color: #f77b17;
                    }
                    .signup-slider-page_label {
                        font-size: .8rem;
                        margin: 0 1.25em; //2.75em
                        opacity: .7;
                        line-height: 24px;
                    }
                }
            }
        }
    }
    .slick-dots li button:before {
        font-size: 10px;
    }
    .slick-dots {
        bottom: 94px; //214px;
        li {
            margin: 0;
        }
    }
}

.logo-with-text {
    width: 160px;
    padding: 0 .5em .5em;
}

.signup-slider-page_buttons-wrapper {
    height: auto !important;
    display: flex;
    justify-content: flex-end;
}
.screen-wrapper {
    overflow: hidden;
    background-repeat: no-repeat;
    position: relative;
    height: 500px !important;
    &--first {
        background-image: url(./assets/images/badge-4.svg);
        background-size: 109%;
        background-position: -98px -25px;
        img {
            width: 127px;
            border-radius: 1000px;
            position: absolute;
            opacity: 0;
        }
        .a {
            top: 100px;
            left: 72px;
        }
        .b {
            bottom: 41px;
            left: 59px;
        }
        .c {
            right: 45px;
            top: 14px;
        }
        .d {
            bottom: 68px;
            right: -20px;
        }
    }
    &--middle {
        position: relative;
        background-image: url(./assets/images/badge-1.svg);
        background-size: 225%;
        background-position: -24px 48px;
        .aa {
            position: absolute;
            background-color: $gray;
            width: 400px;
            padding: 2px 0;
            opacity: 0;
            &:first-of-type {
                top: 37px;
                right: -65px;
                transform: rotate(7deg);
            }
            &:last-of-type {
                bottom: 114px;
                transform: rotate(-17deg);
            }
        }
    }
    &--last {
        position: relative;
        background-image: url(./assets/images/badge-3.svg);
        background-size: 111%;
        background-position: 66px 56px;
    }
}

@keyframes image {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.setup-pages-slider {
    height: 82% !important;
    .slick-dots {
        bottom: -41px;
        width: auto;
        li {
            width: 40px;
            height: 3px;
            margin: 0;
            padding: 0 4px;
            button {
                width: 40px;
                height: 3px;
                padding: 0;
                background-color: #C4C4C4;
                &::before {
                    content: '';
                }
            }
        }
        .slick-active {
            width: 40px;
            button {
                background-color: #000;
            }
        }
    }
}

.setup-screen {
    height: auto !important;
    flex-direction: column;
    justify-content: center;
    display: flex !important;
    .signup-slider-page_label {
        padding: .5em .5em 0 .5em; // 1em
        margin: 0;
        opacity: .7;
        line-height: 24px;
        width: 85%;
        font-size: .8rem;
    }
    &--zero { }
    &--first { }
    &--last {
        position: relative;
        .hello {
            opacity: 0;
            position: absolute;
            bottom: 108px;
            left: -200px;
            width: 64%;
            margin-top: auto;
            justify-self: flex-end;
            font-size: 1.4rem;
            margin-left: .5em;
        }
    }
}

.main-co {
    padding-bottom: 20px;
}

.setup-page {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    max-width: 410px;
    width: 100%;
    height: 100%; //calc(100% - 2.5em*2);
    position: relative;
    // padding: 2.5em 0;
    .signup-slider-page_label {
        font-size: .8rem;
        // padding: 2.5em 1em;
        margin: 0;
        opacity: .7;
        line-height: 24px;
    }
}